:root {
  --font-size-s: 14px;
  --font-size-m: 20px;
  --font-size-l: 30px;

  --color-primary: #7FBC8C;
  --color-secondary: #FFC700;
  --color-accent: #746BEB;
  --color-accent-variant: #F3CEB4;
  --color-black: #000;
  --color-white: #FFF;

  --border-width: 1px;

  --shadow-tilt-s: 2px;
  --shadow-tilt-m: 4px;
  --shadow-tilt-l: 10px;
}

@media screen and (min-width: 426px) {
  :root {
    --border-width: 2px;
    --shadow-tilt-s: 4px;
    --shadow-tilt-m: 8px;
  }
}

@media screen and (min-width: 769px) {
  :root {
    --font-size-s: 16px;
    --font-size-m: 24px;
    --font-size-l: 40px;
  }
}

@media screen and (min-width: 1025px) {
  :root {
    --font-size-l: 50px;
    --shadow-tilt-l: 20px;
  }
}

@media screen and (min-width: 1441px) {
  :root {
    --font-size-l: 60px;
  }
}

@media screen and (min-width: 1630px) {
  :root {
    --font-size-l: 80px;
  }
}

@keyframes slide-up-fade-in {
  0% {
    opacity:0;
    transform:  translate(0px, 50px);
  }
  100% {
    opacity:1;
    transform:  translate(0px, 0px);
  }
}

@keyframes flip-x {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(180deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}

@keyframes flip-y {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(180deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
